
import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './shuffle.scss';

const ShuffleButton = ({playerRef}) => { 
    const onClickPrev = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "previous-shuffle-sample",
            body: true
        }, "*");
    }, [playerRef]);

    const onClickNext = useCallback(() => {
        playerRef.current.contentWindow.postMessage({
            type: "next-shuffle-sample",
            body: true
        }, "*");
    }, [playerRef]);

    return (
        <div className="shuffle-buttons">
          <div className="pushable prev shuffle-btn" onClick={onClickPrev}>
            <div className="circle-icon"/>
          </div>
          <div className="pushable next shuffle-btn" onClick={onClickNext}>
            <div className="right-triangle-icon"/>
          </div>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ShuffleButton);
